<template>
    <div class='form__content'>
        <div class='title'>INHABITANTS</div>
        <div class='section'>
            <input-number :fieldLabel="'HUMANS'" v-model='humans' v-on:input='setHumans()'/>
            <input-number :fieldLabel="'FOOD (kg)'" v-model='food' v-on:input='setFood()'/>
            <input-select :fieldLabel="'CREW QUARTERS'" v-model='crewQuarters' v-on:input='setCrewQuarters()' :options="crewQuartersOptions"/>
            <input-number :fieldLabel="'ECLSS'" v-model='eclss' v-on:input='setECLSS()'/>
        </div>
    </div>
</template>

<script>
import InputSelect from '@/components/Input_Select';
import InputNumber from '@/components/Input_Number';
import {mapState,mapMutations} from 'vuex'
export default {   
    components:{
        'input-select':InputSelect,
        'input-number':InputNumber,
    },

    data(){
        return{
            humans:0,
            food:0,
            crewQuarters:0,
            eclss:0,

            crewQuartersOptions:[
                {
                    'value':'none',
                    'key':'none',
                    'text':'None'
                },
                {
                    'value':'small',
                    'key':'small',
                    'text':'Small - Crew Quarters'
                },
                {
                    'value':'medium',
                    'key':'medium',
                    'text':'Medium - Crew Quarters'
                },
                {
                    'value':'large',
                    'key':'large',
                    'text':'Large - Crew Quarters'
                },
            ],
        }
    },

    methods:{
        ...mapMutations([
            'SETHUMANS',
            'SETECLSS',
            'SETFOOD',
            'SETCREWQUARTERS',
        ]),

        setHumans:function(){
            this.SETHUMANS(this.humans);
        },
        
        setFood:function(){
            this.SETFOOD(this.food);
        },

        setECLSS:function(){
            this.SETECLSS(this.eclss);
        },

        setCrewQuarters:function(){
            this.SETCREWQUARTERS(this.crewQuarters);
        }

    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_config_form';
        
    .form__content{
        @extend %form__content;

        .title{
            @extend %form__section__title;
            margin-bottom: 16px;
        }

        .section{
            @extend %form__section;
        }
    }
</style>
