<template>
    <div class='form__content'>
        <div class='title'>POWER</div>
        <div class='section'>
            <div class='line__item--flex'>                
                <input-select :fieldLabel="powerSourceLabel" :options="powerSourceOptions" v-model='powerSource' v-on:input='setPowerSource()'/>                
                <input-number :fieldLabel="solarArrayLabel" v-model='solarArrays' v-on:input='setSolarArrays()'/>
                <input-number :fieldLabel="powerStorageLabel" v-model='powerStorage' v-on:input='setPowerStorage()'/>
            </div>
        </div>
    </div>
</template>

<script>
import InputSelect from '@/components/Input_Select';
import InputNumber from '@/components/Input_Number';
import {mapState,mapMutations} from 'vuex'
export default {   
    components:{
        'input-select':InputSelect,
        'input-number':InputNumber,
    },

    data(){
        return{
            powerSourceLabel:'POWER SOURCE',
            powerStorageLabel:'BATTERIES',
            solarArrayLabel:'SOLAR ARRAY',

            powerStorage:0,
            powerSource:'',
            solarArrays:0,

            powerSourceOptions:[
                {
                    'value':'solar',
                    'key':'solar',
                    'text':'Solar Arrays',
                },
                {
                    'value':'isotope',
                    'key':'isotope',
                    'text':'Radio Isotope Generator',
                },

            ],
        }
    },

    methods:{
        ...mapMutations([
            'SETPOWERSTORAGE',
            'SETPOWERSOURCE',
            'SETSOLARRAYS',
        ]),

        setPowerStorage:function(){
            this.SETPOWERSTORAGE(this.powerStorage);
        },

        setPowerSource:function(){
            this.SETPOWERSOURCE(this.powerSource);
        },

        setSolarArrays:function(){
            this.SETSOLARRAYS(this.solarArrays)
        }

    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_config_form';
        
    .form__content{
        @extend %form__content;

        .title{
            @extend %form__section__title;
            
        }

        .section{
            @extend %form__section;
        }

        .line__item--flex{
            @extend %form__line__item--flex;
        }
    }
</style>
