import ConnectionHandler from '@/javascript/connectionHandler'

export default{
    state:{
        baselineModel:'plantsonly',
        location:'',
        permanentStay:false,
        durationLength:0,
        durationUnits:'',
        humans:0,
        food:0,
        crewQuarters:'none',
        eclss:0,
        greenhouse:'none',
        powerSource:'',
        solarArrays:0,
        powerStorage:0,


        plantSpecies:[
            {'species':'','amount':0},
            {'species':'','amount':0},
            {'species':'','amount':0},
            {'species':'','amount':0},
            {'species':'','amount':0},
            {'species':'','amount':0},
        ],    

        greenhouseAttributes:{
            'none':{'agent':'','availableSpace':0},
            'small':{'agent':'greenhouse_small','availableSpace':267},
            'medium':{'agent':'greenhouse_medium','availableSpace':1338},
            'large':{'agent':'greenhouse_large','availableSpace':3060}
        },
        
        crewQuartersAttributes:{
            'none':{'agent':''},
            'small':{'agent':'crew_habitat_small'},
            'medium':{'agent':'crew_habitat_medium'},
            'large':{'agent':'crew_habitat_large'} 
        },

        solarArrayAttributes:{
            'mars':{'agent':'solar_pv_array_mars'},
            'moon':{'agent':'solar_pv_array_moon'}
        }
    },

    getters:{
        getPlantSpecies: state => state.plantSpecies,

        greenhouseAvailable: state => state.greenhouseAttributes[state.greenhouse].availableSpace,

        calcGreenhouseRemaining: state =>{            
            let size = state.greenhouse;
            let usedSpace = 0;
            let available = state.greenhouseAttributes[size].availableSpace;


            state.plantSpecies.forEach(element => {
                if(element.amount != null && element.amount != ''){
                    usedSpace = usedSpace + parseInt(element.amount);
                }
            });
 
            let remaining = available - usedSpace;
            return Math.max(0,remaining);
        },

        getFormattedConfig: state =>{
             let params = {"game_config": {
                "duration" : {"value": parseInt(state.durationLength), "type" : state.durationUnits},
                "human_agent": {"amount" : parseInt(state.humans)},
                "habitat" : state.crewQuartersAttributes[state.crewQuarters].agent,
                "greenhouse" : state.greenhouseAttributes[state.greenhouse].agent,
                "food_storage":{"amount":parseInt(state.food)},
                "solar_arrays" : {"amount":parseInt(state.solarArrays)},
                "power_storage" : {"amount":parseInt(state.powerStorage)} ,
                "plants":new Array()
                }
            }
 
            state.plantSpecies.forEach(element => {
                params.game_config.plants.push({"species":element.species,"amount":parseInt(element.amount)})
            })
            
            return params;
        },


        getSolarArrays: state =>{
            return state.solarArrays;
        },

        getPowerStorage: state =>{
            return state.powerStorage;
        },

        getECLSS: state =>{
            return state.eclss;
        },

        getStructureAgents: state => {
            let powerConsumers = new Array();
            powerConsumers.push(state.crewQuartersAttributes[state.crewQuarters].agent);
            powerConsumers.push(state.greenhouseAttributes[state.greenhouse].agent);
            return powerConsumers;
        },

        getGreenhouseAgent:state => {
            console.log(state.greenhouseAttributes[state.greenhouse].agent)
            return state.greenhouseAttributes[state.greenhouse].agent;
        },
        
        getCrewQuartersAgent:state =>{
            return state.crewQuartersAttributes[state.crewQuarters].agent;
        },

        getMissionLength: state => {
            let length = state.durationLength;
            let units = state.durationUnits;
            
            return state.durationLength;

            if(units === 'sols'){
                return length * 24.67
            } else if(units === 'years'){
                return length * 8760;
            } else{
                return length;
            }
        },

        getCrewQuarters: state =>{
            return state.crewQuarters;
        },

        getGreenhouse: state =>{
            return state.greenhouse;
        }
    },

    mutations:{
        SETBASELINEMODEL(state,value){
            state.baselineModel = value;

            //if the value is a preset run 
            // all the mutations.
        },

        SETLOCATION(state,value){
            state.location = value;
        },

        SETPERMANENTSTAY(state,value){
            state.permanentStay = value;        
        },

        SETDURATIONLENGTH(state,value){
            state.durationLength = value;
        },

        SETDURATIONUNITS(state,value){
            state.durationUnits = value;
        },

        SETHUMANS(state,value){
            state.humans = value;
        },

        SETFOOD(state,value){
            state.food = value;
        },

        SETCREWQUARTERS(state,value){
            state.crewQuarters = value;
        },

        SETECLSS(state,value){
            state.eclss = value;
        },

        SETGREENHOUSE(state,value){
            state.greenhouse = value;
        },

        SETPLANTSPECIES(state,value){
            state.plantSpecies[value.index].species = value.species;
            state.plantSpecies[value.index].amount = value.amount;            
        },

        SETPOWERSOURCE(state,value){
            state.powerSource = value;
        },

        SETSOLARRAYS(state,value){
            state.solarArrays = value;
        },

        SETPOWERSTORAGE(state,value){
            state.powerStorage = value;
        },
    }
}