<template>
    <div class='form__content'>
        <div class='title'>INITIAL SETTINGS</div>
        <div class='section'>
            <input-select :fieldLabel="baselineModelLabel" :options="baselineModelOptions" v-model='baselineModel' v-on:input='setBaselineModel()'/>
            <input-select :fieldLabel="locationLabel" :options="locationOptions" v-model='location' v-on:input='setLocation()'/>
            <div class='line__item--flex'>
                <input-number :fieldLabel="durationLengthLabel" v-model='durationLength' v-on:input='setDurationLength()'/>
                <input-select :fieldLabel="durationUnitsLabel" :options="durationUnitOptions" v-model='durationUnits' v-on:input='setDurationUnits()'/>
            </div>
        </div>
    </div>
</template>

<script>
import InputSelect from '@/components/Input_Select';
import InputNumber from '@/components/Input_Number';
import {mapState,mapMutations} from 'vuex'
export default {   
    components:{
        'input-select':InputSelect,
        'input-number':InputNumber,
    },

    data(){
        return{
            baselineModel:'',
            location:'',
            permanentStay:false,
            durationLength:0,
            durationUnits:'',

            baselineModelLabel:'MODEL',
            locationLabel:'LOCATION',
            durationLengthLabel:'DURATION',
            durationUnitsLabel:'UNITS',

            baselineModelOptions:[
                {
                    'value':'simocbase',
                    'key':'simocbase',
                    'text':'SIMOC Baseline',
                },
                {
                    'value':'humansonly',
                    'key':'humansonly',
                    'text':'Humans Only',
                },
                {
                    'value':'plantsonly',
                    'key':'plantsonly',
                    'text':'Plants Only',
                },

            ],

            locationOptions:[
                {
                    'value':'mars',
                    'key':'mars',
                    'text':'Mars'
                },
                {
                    'value':'moon',
                    'key':'moon',
                    'text':"Earth's Moon"
                },
            ],

            durationUnitOptions:[
                {
                    'value':'hour',
                    'key':'hours',
                    'text':'Hours'
                },
                {
                    'value':'day',
                    'key':'days',
                    'text':'Sols',
                },
                {
                    'value':'year',
                    'key':'years',
                    'text':'Years',
                }
            ]

        }
    },

    methods:{
        ...mapMutations([
            'SETBASELINEMODEL',
            'SETLOCATION',
            'SETDURATIONLENGTH',
            'SETDURATIONUNITS',
        ]),

        setBaselineModel:function(){

            this.SETBASELINEMODEL(this.baselineModel);
        },

        setLocation:function(){
            this.SETLOCATION(this.location);
        },

        setDurationLength:function(){
            this.SETDURATIONLENGTH(this.durationLength);
        },

        setDurationUnits:function(){
            this.SETDURATIONUNITS(this.durationUnits);
        }

    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_config_form';
        
    .form__content{
        @extend %form__content;

        .title{
            @extend %form__section__title;
            
        }

        .section{
            @extend %form__section;
        }

        .line__item--flex{
            @extend %form__line__item--flex;
        }
    }
</style>
